@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,900;1,100;1,400;1,500;1,700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", arial, sans-serif;
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}

.component {
  display: grid;
  grid-template-columns: repeat(2, minmax(200px, 1fr));
  width: 1440px;
  height: auto;
  overflow: hidden;
}
.component > .component-image {
  width: 505px;
  height: 330px;
  margin: 96px 95px 0 230px;
}
.component > .component-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin: 136px 0 230px 95px;
}
.component > .component-content .component-header {
  font-size: 42px;
  font-weight: bold;
  line-height: 50.4px;
}
.component > .component-content .component-paragraph {
  width: 380px;
  height: 96px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  margin-top: 24px;
  color: #121212;
}
.component > .component-content .component-button {
  width: auto;
  height: 48px;
  line-height: 100%;
  margin-top: 32px;
  font-size: 16px;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  background-color: #e4c1ae;
  color: #ffffff;
  cursor: pointer;
}

.modal-background {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100vw;
  border: none;
  background-color: rgba(0, 0, 0, 0.5);
}
.modal-background > .modal {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: start;
  margin: 0 auto;
  height: 210px;
  width: 920px;
  border: none;
}
.modal-background > .modal > h2 {
  margin: 56px 0 0 95px;
  width: 380px;
  height: 50px;
  font-size: 42px;
  line-height: 50.4px;
  font-weight: 700;
  color: #121212;
}
.modal-background > .modal > p {
  width: 380px;
  height: 24px;
  margin: 24px 0 56px 95px;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
}
.modal-background > .modal > button.close-modal-button {
  position: absolute;
  top: 46px;
  right: 46px;
  width: 12px;
  height: 12px;
  font-weight: 700;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
.modal-background > .modal > button.reset-user-counter-btn {
  position: absolute;
  bottom: 26px;
  right: 46px;
  width: auto;
  height: 48px;
  line-height: 100%;
  margin-top: 32px;
  font-size: 16px;
  border: none;
  border-radius: 32px;
  padding: 16px 40px;
  font-weight: 700;
  background-color: #e4c1ae;
  color: #ffffff;
  cursor: pointer;
}